.image-container {
  width: 90vw;
  left: 5vw;
  position: absolute;
}
.image-container h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}
.grid-display {
  column-count: 4;
}

.grid-display img {
  margin-bottom: 0.8em;
}
.image-gallery {
  position: relative;

  top: 0;
  left: 0;
  width: 100vw;
  min-height: 300vh;
  background-color: rgb(0, 0, 0, 0.6);
}
#close {
  position: relative;
  cursor: pointer;
  left: 8px;
  top: 8px;
  color: rgb(165, 165, 165);
  font-size: 2.4rem;
}
#arrow {
  color: rgb(165, 165, 165);
}
.img-full {
  position: relative;
  background-color: white;
  width: 80%;
  height: 48%;
  margin: 0px auto;
  border-radius: 5px;
  /* top: 30px; */
  text-align: center;
}
.img-full img {
  height: 80vh;
}
.img-full p {
  font-size: 14px;
  text-align: left;
}

.prev {
  position: relative;
  top: 44vh;
  left: -880px;
}
.next {
  position: relative;
  bottom: 40vh;
  right: -880px;
}
.p-up {
  position: relative;
  left: 10px;
  top: 10px;
}

.p-down {
  position: relative;
  left: 10px;
  bottom: 20px;
}

.input-search-result {
  text-align: center;
}
.div-input-search {
  border-top-left-radius: 23px;
  border-bottom-left-radius: 23px;
  border-bottom-right-radius: 23px;
  border-top-right-radius: 23px;
  margin: 1% auto;
  font-family: Inter, FontAwesome;
  font-size: 1.3rem;
  width: 60%;
  padding: 10px;
  background-color: rgb(231, 231, 231);
  color: rgb(124, 124, 124);
  text-align: left;
  padding-left: 25px;
  margin-top: 50px;
  margin-bottom: 30px;
}
.div-input-search input {
  display: inline;
  background-color: rgb(255, 255, 255, 0);
  border: none;
  width: 50%;
  padding-left: 20px;
  font-size: 1rem;
  font-weight: 500;
  color: rgb(78, 78, 78);
  outline: 0;
}
.div-input-search p {
  display: inline;
  text-align: right;
  font-weight: 100;
  margin-left: 450px;
}
